@use "../mixins/title.scss";
@use "../mixins/media.scss";
@use "../variables.scss";

.fieldset {
  // display: flex;
  // flex-direction: column;
  margin: 0;
  box-sizing: border-box;

  @include media.for-mobile {
    padding: 20px 20px 24px 20px;
  }

  @include media.for-tablet {
    padding: 20px 0 24px;
    // TODO
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.fieldset__title {
  @include title.title();
  text-align: center;
  margin: 0 0 20px;

  @media (min-width: variables.$tablet-width) {
    @include title.title-medium();

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      height: 1px;
      width: 100%;
      background-color: #dbdbdb;
      z-index: -1;
      left: calc(100% + 40px);
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      height: 10px;
      width: 100%;
      background-color: #bd2727;
      z-index: -1;
      left: calc(100% + 40px);
    }
  }
}

.fieldset__fields {
  list-style: none;
  margin: 0;
  padding: 0;


  @include media.for-tablet {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    min-width: 480px;
  }

  @media (min-width: variables.$desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 64px;
  }
}

.fieldset__field {
  flex-direction: column;
}

@include media.for-mobile {
  .fieldset__field + .fieldset__field {
    margin-top: 16px;
  }
}

